.Persik {
  display: block;
  width: 100%;
  max-width: 240px;
  margin: 20px auto;
}


@font-face {
  font-family: 'San Francisco Pro Display Thin';
  src: url('SF-Pro.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  }


.box-name {font-family: 'San Francisco Pro Display Thin';}

.parent {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.svg{
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.title{

  position: absolute;
  left: 20%;
  top: 10%;
  -webkit-transform: translate(-30%, -10%);
  -moz-transform: translate(-30%, -10%);
  -ms-transform: translate(-30%, -10%);
  -o-transform: translate(-30%, -10%);
  transform: translate(-30%, -10%);

  size: 16;
}



.button{
  position: absolute;
  bottom: 10%;
  left: 50%;
}

.discription{
  position: absolute;
  left: 20%;
  top: 20%;
  -webkit-transform: translate(-30%, -20%);
  -moz-transform: translate(-30%, -20%);
  -ms-transform: translate(-30%, -20%);
  -o-transform: translate(-30%, -20%);
  transform: translate(-30%, -20%);
}
